<template>
  <div style="width: 900px;">
    <h5>{{textDic.title}}</h5>
    <div style="width: 900px; height: 1px; background-color: #333;margin: 20px 0;"></div>
    <Row>
      <Col span="24">
        <div class="addBtn">
          <Button type="primary" @click="didClickAdd">{{textDic.text0}}</Button>
        </div>
        <table border="1px" cellspacing="0" cellpadding="0">
          <tr>
            <th v-for="(title, i) in textDic.tableTitles" :key="i">{{title}}</th>
          </tr>
          <tr v-for="(formItem, index) in formItems" :key="index">
            <td v-for="(text, j) in formItem" :key="j">
              <custom-cell :index="index" :cellData="cellText(j, text)" :type="cellType(j)" @didClickButton="didClickButton"></custom-cell>
            </td>
          </tr>
        </table>
    <Modal
        v-model="modal"
        :title="textDic.text1"
        @on-ok="didClickModel(true)"
        @on-cancel="didClickModel(false)">
        <Form :label-width="80">
          <!-- <Form-item :label="textDic.editTitles[0]"> -->
            <Form-item :label="test()">
            <Input v-model="editForm[0]"/>
          </Form-item>
          <Form-item :label="textDic.editTitles[1]">
            <Radio-group v-model="editForm[1]" v-for="(text, index) in textDic.text3" :key="index">
              <Radio :label="textDic.text3[index]">{{textDic.text3[index]}}</Radio>
            </Radio-group>
          </Form-item>
          <Form-item :label="textDic.editTitles[2]">
            <Checkbox-group v-model="editForm[2]" v-for="(text, index) in textDic.text4" :key="index">
                <Checkbox :label="text">{{text}}</Checkbox>
            </Checkbox-group>
          </Form-item>
        </Form>
    </Modal>
      </Col>
    </Row>
  </div>
</template>

<script>
import CustomCell from '../components/CustomCell'
export default {
  components: {
    CustomCell
  },

  data() {
    return {
      formItems: [
        ['0', '1', ''],//['超级管理员', '正常', '编辑'],
        ['1', '0', '']//['客服', '停用', '编辑'],
      ],
      modal: false,
      currentIndex: 0,
      editForm: ['', '', ''],
    }
  },
  methods: {
    test() {
      return JSON.stringify(this.checked);
    },
    didClickAdd() {

    },
    didClickButton(data) {
      this.currentIndex = data.index;
      this.modal = true;
    },
    didClickModel(isEdit) {
      // 编辑的保存与否
    },
    cellType(j) {
      if(j == 2) {
        return 'button';
      }else {
        return 'text';
      }
    },
    cellText(j, text) {
      var result = [];
      if(j == 0){
        result = text == '0' ? [this.textDic.text2[0]] : [this.textDic.text2[1]];
      }else if(j == 1) {
        result = text == '0' ? [this.textDic.text3[0]] : [this.textDic.text3[1]];
      }else {
        result = [this.textDic.text1]
      }
      return result;
    }
  },
  computed:{
    textDic() {
      return {
        title: '角色管理',
        text0: '添加',
        tableTitles: ['角色名', '状态', '操作'],
        text1: '编辑',
        text2: ['超级管理员', '客服'],
        text3: ['停用', '正常'],
        editTitles: ['角色名', '账户状态', '权限设置'],
        text4: ['账号管理', '角色管理', '代理商信息']
      }
    }
  }

}
</script>

<style scoped>
.addBtn {
    width: 900px; 
    background-color: #333;
    padding: 5px 10px;;
  }
  td, th {
    width: 300px; 
    height: 60px;
  }
</style>



